@use '../abstracts/constants';

.main-login {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.auth-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: center;
    width: 400px;
    padding: 40px;
    border-radius: 10px;
    background-color: constants.$color-login-background;
}

.user-name-row,
.password-row {
    display: flex;
    flex-direction: column;
    width: 70%;
}

.login-button {
    padding: 6px 20px;
    border: none;
    border-radius: 6px;
    font-size: 18px;
    background-color: constants.$color-login-button;
}

.login-info-button {
    padding: 6px 20px;
    border: none;
    border-radius: 6px;
    font-size: 18px;
    background-color: constants.$color-login-button;
}

.login-button:hover,
.login-info-button:hover {
    background-color: constants.$color-login-input-background;
}

.user-name-input,
.password-input {
    padding: 6px 12px;
    border: 1px solid constants.$color-login-input-border;
    border-radius: 6px;
    background-color: constants.$color-login-input-background;
}
