@use '../abstracts/constants';

.dark-view {
    position: fixed;
    top: 0;
    left: 0;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: constants.$color-error-message-background-outside;
}

.modal {
    position: relative;
    width: 450px;
    margin: 0 auto;
    padding: 20px;
    border-radius: 20px;
    background-color: constants.$color-error-message-background;
}

.icon-close {
    cursor: pointer;
    position: absolute;
    top: 12px;
    right: 12px;
}

.modal-text {
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 25px;
}
